"use client"
import React from 'react'
import { useAudio } from './AudioProvider'

export default function MiniPlayer() {
  const { currentTrack, isPlaying, togglePlay, currentTime, duration, seek, setVolume } = useAudio()

  if (!currentTrack) return null

  const format = (s: number) => {
    if (!s || !isFinite(s)) return '0:00'
    const m = Math.floor(s / 60)
    const sec = Math.floor(s % 60).toString().padStart(2, '0')
    return `${m}:${sec}`
  }

  const pct = duration ? (currentTime / duration) * 100 : 0

  return (
    <div className="fixed bottom-4 left-1/2 transform -translate-x-1/2 bg-neutral-800/90 border border-neutral-700 rounded-md px-4 py-3 shadow-lg z-50 w-[min(48rem,95%)]">
      <div className="flex items-center gap-4">
        <div>
          <div className="text-sm font-medium">{currentTrack.title}</div>
          <div className="text-xs text-neutral-400">{currentTrack.artist}</div>
        </div>
        <button aria-label={isPlaying ? 'Pause' : 'Play'} onClick={togglePlay} className="px-3 py-2 bg-neutral-700 rounded focus-ring">
          {isPlaying ? 'Pause' : 'Play'}
        </button>
        <div className="flex-1">
          <input aria-label="Seek" className="w-full" type="range" min={0} max={duration || 0} value={currentTime} onChange={(e) => seek(Number(e.target.value))} />
          <div className="flex justify-between text-xs text-neutral-400 mt-1">
            <span>{format(currentTime)}</span>
            <span>-{format(Math.max(0, duration - currentTime))}</span>
          </div>
        </div>
        <input aria-label="Volume" type="range" min={0} max={1} step={0.01} defaultValue={1} onChange={(e) => setVolume(Number(e.target.value))} className="w-24" />
      </div>
    </div>
  )
}
