"use client"
import React, { createContext, useContext, useEffect, useRef, useState } from 'react'

type Track = {
  id: string
  title: string
  src: string
  artist?: string
}

type AudioContextValue = {
  currentTrack: Track | null
  isPlaying: boolean
  playTrack: (t: Track) => void
  togglePlay: () => void
  pause: () => void
  seek: (time: number) => void
  setVolume: (v: number) => void
  currentTime: number
  duration: number
}

const AudioCtx = createContext<AudioContextValue | undefined>(undefined)

export const useAudio = () => {
  const c = useContext(AudioCtx)
  if (!c) throw new Error('useAudio must be used within provider')
  return c
}

export const AudioPlayerProvider = ({ children }: { children: React.ReactNode }) => {
  const audioRef = useRef<HTMLAudioElement | null>(null)
  const [currentTrack, setCurrentTrack] = useState<Track | null>(null)
  const [isPlaying, setIsPlaying] = useState(false)
  const [currentTime, setCurrentTime] = useState(0)
  const [duration, setDuration] = useState(0)

  useEffect(() => {
    if (!audioRef.current) audioRef.current = new Audio()
    const audio = audioRef.current

    const onTime = () => setCurrentTime(audio.currentTime)
    const onDuration = () => setDuration(audio.duration || 0)
    const onEnd = () => setIsPlaying(false)

    audio.addEventListener('timeupdate', onTime)
    audio.addEventListener('loadedmetadata', onDuration)
    audio.addEventListener('ended', onEnd)

    return () => {
      audio.removeEventListener('timeupdate', onTime)
      audio.removeEventListener('loadedmetadata', onDuration)
      audio.removeEventListener('ended', onEnd)
    }
  }, [])

  useEffect(() => {
    const onKey = (e: KeyboardEvent) => {
      if (['INPUT', 'TEXTAREA'].includes((e.target as HTMLElement)?.tagName)) return
      if (e.code === 'Space') {
        e.preventDefault()
        togglePlay()
      }
      if (e.key === 'ArrowRight') seek((audioRef.current?.currentTime || 0) + 5)
      if (e.key === 'ArrowLeft') seek((audioRef.current?.currentTime || 0) - 5)
    }
    window.addEventListener('keydown', onKey)
    return () => window.removeEventListener('keydown', onKey)
  }, [currentTrack, isPlaying])

  const playTrack = (t: Track) => {
    const audio = audioRef.current!
    if (!audio) return
    if (currentTrack?.src !== t.src) {
      audio.src = t.src
      setCurrentTrack(t)
    }
    audio.play().then(() => setIsPlaying(true)).catch(() => setIsPlaying(false))
  }

  const togglePlay = () => {
    const audio = audioRef.current
    if (!audio) return
    if (isPlaying) { audio.pause(); setIsPlaying(false) }
    else { audio.play().then(() => setIsPlaying(true)).catch(() => setIsPlaying(false)) }
  }

  const pause = () => { audioRef.current?.pause(); setIsPlaying(false) }
  const seek = (t: number) => { if (audioRef.current) audioRef.current.currentTime = Math.max(0, Math.min((duration || Infinity), t)) }
  const setVolume = (v: number) => { if (audioRef.current) audioRef.current.volume = v }

  return (
    <AudioCtx.Provider value={{ currentTrack, isPlaying, playTrack, togglePlay, pause, seek, setVolume, currentTime, duration }}>
      {children}
    </AudioCtx.Provider>
  )
}
